p{
  padding: 0;
  margin: 0;
}

h1,h2,h3,h4,h5,h6{
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("images/kge1XLqxA2QЧБ_3.png");
  background-color: rgba(237, 189, 99, 0.8);
  background-repeat: repeat;
  background-size: 180px 100px;
  background-blend-mode: multiply;
  overflow: hidden;
}
.App_container{
  max-width: 1200px;
  width: 100%;
  height: 100vh;

  display: grid;

  grid-gap: 2vh;
  grid-template-areas:
          "header header"
          "chat user";

  grid-template-rows: 10vh 1fr;
  grid-template-columns: 3fr 1fr;

}
/***********************/
.Header{
  grid-area: header;
  background-color: rgb(255, 255, 255);
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
}
.Header_main{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.slogan{
  padding-right: 15px;
  font-size: 24px;
  color: #000;
}
.logo{
  margin-left: 20px;
}
.logo img{
  width: auto;
  height: 10vh;
  transition: all 0.3s ease;
}
.logo:hover img{
  transform: scale(10) rotate(1800deg);
}
/***********************************/



.User{
  grid-area: user;
  background-color: rgb(255, 255, 255);
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  padding: 10px;
  display: flex;
  justify-content: start;
  overflow-y: auto;
  flex-direction: column;
  align-items: center;
}
.User > h2{
  margin-bottom: 5px;
}
.User > span{
  font-size: 24px;
  margin-bottom: 5px;
}
@media (max-width: 700px) {
  .User{
    display: none;
  }
  .App_container{
    grid-template-areas:
            "header"
            "chat ";

    grid-template-rows: 10vh;
    grid-template-columns: 1fr;
  }
  .slogan{
    padding-right: 15px;
    padding-left: 15px;
    text-align: right;
  }
}

/*************************************/
.Chatick{
  grid-area: chat;

}
.Chatick>div:first-child{

  height: 75vh;
  width: 100%;

  overflow-y: auto;
}

.Chatick>form{
  height: 13vh;
  width: 100%;
  background-color: #fff;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;

}

.Vvod{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.vvod_pepe{
  width: 60px;
  height: 60px;
  margin-left: 15px;
  filter: grayscale(1);
  transition: all 0.5s ease;
}
.vvod_pepe:hover{
  filter: grayscale(0);
}
.vvod_send{
  margin-right: 15px;
  font-size: 20px;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 5px 15px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.vvod_send:hover{
  background-color: black;
  color: #fff;
}
.vvod_vvod_input{
  height: 40px;
  width: 65%;
  margin: 0 10px;
  font-size: 24px;
  resize: none;
}

.MessageMe{
  max-width: 48%;
  background-color: #fff;
  padding: 10px;
  border-radius: 10px;
}

.MessageMe p{
  word-wrap: break-word;
  max-width: 1000px;
  text-align: right;
}

.Message{
  max-width: 48%;
  background-color: #fff;
  padding: 10px;
  border-radius: 10px;
}

.Message p{
  word-wrap: break-word;
  max-width: 1000px;

}

.Message_container{
  width: 100%;
  display: flex;
  margin: 10px 0;
  justify-content: flex-start;
}
.MessageMe_container{
  width: 100%;
  display: flex;
  margin: 10px 0;
  justify-content: flex-end;
}

.Message_name{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.Message_name h4{
  font-size: 18px;
  margin-right: 10px;
}
.Message_name span{
  font-size: 14px;
  color: #787878;
}

.Okno::-webkit-scrollbar{
  width:5px;
}
.Okno::-webkit-scrollbar-thumb{
  border-width:1px 1px 1px 2px;
  border-color: #777;
  background-color: #aaa;
  border-radius: 15px;
}
.Okno::-webkit-scrollbar-thumb:hover{

  background-color: #777;
}
.Okno::-webkit-scrollbar-track{
  border-width:0;
}
/***********************/
.Login{
  display: flex;
  align-items: center;
  justify-content: center;
}
.login_con{
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 15px;
  border-radius: 10px;
}
.login_con>span{
  color: #0518b1;
  cursor: pointer;
  font-size: 20px;
  margin-top: 15px;
}
.login_con>span:hover{
  text-decoration: underline;
}
.login_con>h1{
  margin-bottom: 15px;
}

.LoginIn{
  display: flex;
  flex-direction: column;
}
.LoginIn button{
  border: 1px solid #000;
  outline: none;
  background-color: transparent;
  font-size: 20px;
  padding: 5px;
  margin-top: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.LoginIn button:hover{
  color: #fff;
  background-color: #000;
}
.LoginIn input{
  font-size: 20px;
  padding: 5px;
  margin-bottom: 15px;
}
.LoginIn span{
  font-size: 20px;
}

.error{
  text-align: center;
  color: red;
  border: 1px solid red;
  font-size: 20px;
  padding: 5px 0;
  position: relative;
  text-transform: uppercase;
  width: 100%;
}
.error_x{
  font-size: 16px;
  position: absolute;
  top: 8px;
  right: 5px;
  text-transform: uppercase;
  padding: 0 3px;
  border: 1px solid red;
  cursor: pointer;
  transition: all 0.3s ease;
}
.error_x:hover{
  color: #fff;
  background-color: red;
}













